<template>
    <div class="admin" v-fixedDimension>
        <header>
            <router-link to="/Admin">{{logo}}</router-link>
            <el-breadcrumb separator="/">
                <el-breadcrumb-item to="/Admin">
                    <i class="fa fa-home" aria-hidden="true"></i>我们说</el-breadcrumb-item>
                <el-breadcrumb-item v-for="(value,index) in items" :key="index" :to="value.href">{{value.text}}</el-breadcrumb-item>
            </el-breadcrumb>
        </header>
        <section>
            <aside>
                <el-form :inline="true" :model="nav" class="form-inline">
                    <el-input size='mini' v-model="nav.search" placeholder="搜索信息">
                        <el-button @click="onSubmit" slot="append" icon="el-icon-search"></el-button>
                    </el-input>
                </el-form>
                <ul class="menu" ref="menu">
                    <!-- <li v-for="(val,index) in asidemenu" :key="index">
          <p>
            <router-link :to="val.path">{{val}}</router-link>
          </p>
        </li> -->
                    {{asidemenu}}
                </ul>
            </aside>
            <article>
                <router-view></router-view>
            </article>
        </section>
    </div>
</template>

<script>
    //引入外部资源，启用rem
    import '@/scripts/rem.js'
    import aside from '@/scripts/aside.js'
    //单独引入自定义css文件，专门用于修改element.css默认样式
    import '@/style/elementui.css'
    export default {
        name: 'Admin',
        data() {
            return {
                logo: "我们说 - 后台管理系统",
                items: [],
                nav: {
                    search: '',
                    // 通过独立的路由信息aside.js页面获取到左侧导航所有信息列表
                    // 实现复写
                    aside: aside[1].children
                }
            }
        },
        methods: {
            // 面包屑导航 通过路由数据传输
            getBreadcrumb: function () {
                this.items = this.$route.matched[1].meta.title;
            },
            // 导航查找
            onSubmit() {
                alert(this.nav.search)
            }
        },
        computed: {
            asidemenu() {
                let aside = this.nav.aside;
                let arr = [];
                for (let index = 0; index < aside.length; index++) {
                    const el = aside[index];
                    // console.log(el)
                    if (el.meta.title.length > 1) {
                        if (el.meta.title[0].text != "") {
                            // console.log([el.meta.title[0].text,el.meta.title[1].text],el.path)
                            arr.push({
                                text: [el.meta.title[0].text,el.meta.title[1].text],
                                path:el.path
                            })
                            // arr = Array.from(new Set(arr.text))
                        }
                    }
                }
                console.log(arr)
                return arr
            }
        },
        watch: {
            $route(to, from) {
                // 通过监听路由的变化加载对应的展示面包屑导航
                // console.log(to.path,from.path);
                if (to.path != from.path) {
                    this.getBreadcrumb();
                }
            }
        },
        filters: {

        },
        created() {

        },
        mounted() {
            // 展示面包屑导航
            this.getBreadcrumb();
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .admin {
        width: 100%;
    }

    .admin * {
        box-sizing: border-box;
        margin: 0;
        padding: 0
    }

    .admin header {
        width: 100%;
        height: 3.75rem;
        z-index: 2;
        position: fixed;
        background: -webkit-linear-gradient(45deg, #838dfb, #8191f8 10%, #4fa2f1 50%, #1eabf6);
        background: -o-linear-gradient(45deg, #838dfb 0, #8191f8 10%, #4fa2f1 50%, #1eabf6 100%);
        background: linear-gradient(45deg, #838dfb, #8191f8 10%, #4fa2f1 50%, #1eabf6);
        box-shadow: #838dfb 0 0 .3125rem;
        display: flex
    }

    .admin .el-breadcrumb {
        position: absolute;
        bottom: -2.5rem;
        left: 14rem;
        padding-left: 2rem;
        height: 2.5rem;
        line-height: 2.5rem;
        border-bottom: 1px solid rgba(216, 216, 216, .5);
        width: calc(100% - 13.75rem);
        background: -webkit-linear-gradient(left, rgba(255, 255, 255, .5) 5%, #fff 95%);
        /* Safari 5.1 - 6.0 */
        background: -o-linear-gradient(right, rgba(255, 255, 255, .5) 5%, #fff 95%);
        /* Opera 11.1 - 12.0 */
        background: -moz-linear-gradient(right, rgba(255, 255, 255, .5) 5%, #fff 95%);
        /* Firefox 3.6 - 15 */
        background: linear-gradient(to right, rgba(255, 255, 255, .5) 5%, #fff 95%);
        /* 标准的语法 */
    }

    .admin section {
        padding-top: 7rem;
        padding-left: 1rem;
        height: 100%;
        width: 100%;
        display: flex;
        justify-content: space-around;
        flex-wrap: nowrap;
    }

    .admin aside {
        height: 100%;
        padding-top: 3.75rem;
        width: 13.75rem;
        position: fixed;
        z-index: 1;
        top: 0;
        bottom: 0;
        left: 0;
        box-shadow: #838dfb .0625rem 0 .3125rem;
        background: #e2dcf3;
        display: flex;
        flex-flow: column;
        justify-content: flex-start;
    }

    .admin aside .menu {
        display: flex;
        flex-flow: column;
        margin: 0
    }

    .admin aside .menu>li {
        border: 1px solid #000;
        width: 100%;
        display: flex;
        flex-flow: column;
        margin: 5px auto
    }

    .admin aside .menu>div h1 {
        margin: 0;
        padding: 0;
    }

    .admin aside .form-inline {
        width: 96%;
        margin: .625rem auto;
    }

    .admin article {
        width: 100%;
        padding-left: 13.75rem;
        height: 200%;
        background: #fff;
    }

    .admin header .router-link-active {
        color: #fff;
        height: 100%;
        line-height: 3.75rem;
        padding: 0 .9375rem;
    }

    header .fa-home {
        margin-right: .3125rem
    }
</style>
